.product-video {
  .border;
  padding: 21px 0 33px 0;
  display: flex;
  flex-direction: column;
}

.product-video__title {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  .font;
}

.product-video__title::before {
  content: url("../../../../img/mobile/icon-video.svg");
}

.product-video__description {
  max-width: 225px;
  .font;
  font-weight: 400;
  font-size: 13px;
  line-height: 24px;
  align-self: center;
}

.product-information__title--small-width {
  max-width: 225px;
}
